import React from "react";

const imgs1="https://i.ibb.co/QP6Nmpf/image-1-about.jpg";

const imgs2="https://i.ibb.co/JvN0NVB/image-2-about.jpg";

function Abtcard(props){

return (

<>
<section className="about-section">
        <div className="container">
            <div className="row">                
                <div className="content-column col-lg-6 col-md-12 col-sm-12 order-2">
                    <div className="inner-column">
                        <div className="sec-title">
                            <span className="title">{props.abtcom}</span>
                            <h3 className="had">{props.abttitle}</h3>
                        </div>
                   
                        <ul className="lists">
                            <li><i className="fa fa-square"></i> &nbsp; {props.lione}</li>
                            <li><i className="fa fa-square"></i> &nbsp; {props.litwo}</li>
                            <li><i className="fa fa-square"></i> &nbsp; {props.lithree}</li>
                        </ul>
                        <div className="btn-box">
                            <a href="#" className="theme-btn btn-style-one">Contact Us</a>
                        </div>
                    </div>
                </div>

             
                <div className="image-column col-lg-6 col-md-12 col-sm-12">
                    <div className="inner-column wow fadeInLeft">
                        <figure className="image-1"><a href="#" className="lightbox-image" data-fancybox="images"><img src={imgs1} alt=""  className="crds"/></a></figure>
                        <figure className="image-2"><a href="#" className="lightbox-image" data-fancybox="images"><img src= {imgs2}alt=""  /></a></figure>
                    </div>
                </div>
            </div>
        </div>
    </section>

</>

);




}

export default Abtcard;

